import { ThunkType } from '../../../main/bll/store';
import { cardsApi, CardsResponseDataType, CardsType } from '../dal/CardsApi';

export enum packsActionType {
  SET_LOADING_CARDS = 'CARDS/CARDS/SET_LOADING',
  SET_ERROR_CARDS = 'CARDS/CARDS/SET_ERROR_PACKS',
  SET_CARDS = 'CARDS/CARDS/SET_PACKS',
  ADD_CARD = 'CARDS/CARDS/ADD_CARD',
  UPDATE_CARD = 'CARDS/CARDS/UPDATE_CARD',
  DELETE_CARD = 'CARDS/CARDS/DELETE_CARD',
}


const initialState: StateType = {
  cards: [],
  loading: false,
  error: '',
  cardsTotalCount: 0,
  maxGrade: 0,
  minGrade: 0,
  page: 0,
  pageCount: 4,
  packUserId: '',
};

export const cardsReducer = (
  state = initialState,
  action: ActionsType,
): StateType => {
  switch (action.type) {
    case packsActionType.SET_LOADING_CARDS:
      return {
        ...state,
        loading: action.payload.loading,
      };
    case packsActionType.SET_ERROR_CARDS:
      return {
        ...state,
        loading: false,
        error: action.payload.error,
      };
    case packsActionType.SET_CARDS:
      return { ...state, ...action.data };
    default:
      return state;
  }
};


/** Actions */
export const setLoadingCards = (loading: boolean) => ({
  type: packsActionType.SET_LOADING_CARDS,
  payload: {
    loading,
  },
} as const);
export const setCards = (data: CardsResponseDataType) =>
  ({ type: packsActionType.SET_CARDS, data } as const);

export const setErrorCards = (error: string) =>
  ({
    type: packsActionType.SET_ERROR_CARDS,
    payload: {
      error,
    },
  } as const);
export const addCard = (newCard: CardsResponseDataType) =>
  ({
    type: packsActionType.ADD_CARD,
    newCard,
  } as const);
export const updatedCard = (updatedCard: CardsResponseDataType) =>
  ({
    type: packsActionType.UPDATE_CARD,
    updatedCard,
  } as const);
export const deletedCard = (deletedCard: CardsResponseDataType) =>
  ({
    type: packsActionType.DELETE_CARD,
    deletedCard,
  } as const);

/** Thunks */
export const getCardsTC = (cardsPack_id: string, pageCount?: number, page?: number): ThunkType<ActionsType> =>
  async (dispatch) => {

    try {
      dispatch(setLoadingCards(true));
      
      // const {cardPacks, data: {cardPacks}} = await cardsApi.getPacks(userId);
      const data = await cardsApi.getCards({ cardsPack_id, pageCount, page });
      dispatch(setCards(data));
      dispatch(setLoadingCards(false));
    } catch (e) {
      const error = e.response
        ? e.response.data.error
        : (e.message + ', more details in the console');
      dispatch(setErrorCards(error));
    }
  };
export const addCardTC = (cardsPack_id: string, question: string, answer: string,
                          pageCount: number, page: number): ThunkType<ActionsType> =>
  async (dispatch) => {
    try {
      dispatch(setLoadingCards(true));
      let data = await cardsApi.addCard({ cardsPack_id, question, answer });
      await dispatch(getCardsTC(cardsPack_id, pageCount, page));
      dispatch(setLoadingCards(false));
    } catch (e) {
      const error = e.response
        ? e.response.data.error
        : (e.message + ', more details in the console');
      dispatch(setErrorCards(error));
    }
  };
export const updateCardTC = (_id: string, question: string, answer: string, cardsPack_id: string,
                             pageCount: number, page: number): ThunkType<ActionsType> =>
  async (dispatch) => {
    try {
      dispatch(setLoadingCards(true));
      let data = await cardsApi.updatedCard({ _id, question, answer });
      await dispatch(getCardsTC(cardsPack_id, pageCount, page));
      dispatch(setLoadingCards(false));
    } catch (e) {
      const error = e.response
        ? e.response.data.error
        : (e.message + ', more details in the console');
      dispatch(setErrorCards(error));
    }
  };
export const updateGradeCardTC = (cardsPack_id: string, card_id: string, grade: number): ThunkType<ActionsType> =>
  async (dispatch) => {
    try {
      debugger
      dispatch(setLoadingCards(true));
      let data = await cardsApi.updatedGradeCard({ grade, card_id });
      await dispatch(getCardsTC(cardsPack_id));
      dispatch(setLoadingCards(false));
    } catch (e) {
      const error = e.response
        ? e.response.data.error
        : (e.message + ', more details in the console');
      dispatch(setErrorCards(error));
    }
  };
export const deleteCardTC = (cardId: string, cardsPack_id: string,
                             pageCount: number, page: number): ThunkType<ActionsType> =>
  async (dispatch) => {
    try {
      dispatch(setLoadingCards(true));
      let data = await cardsApi.deleteCard(cardId);
      await dispatch(getCardsTC(cardsPack_id, pageCount, page));
      dispatch(setLoadingCards(false));
    } catch (e) {
      const error = e.response
        ? e.response.data.error
        : (e.message + ', more details in the console');
      dispatch(setErrorCards(error));
    }
  };

/** Types */
export type StateType = {
  cards: CardsType[];
  cardsTotalCount: number
  maxGrade: number
  minGrade: number
  page: number
  pageCount: number
  packUserId: string
  loading: boolean;
  error: string;
};

export type ActionsType = ReturnType<typeof setLoadingCards>
  | ReturnType<typeof setCards>
  | ReturnType<typeof addCard>
  | ReturnType<typeof setErrorCards>
  | ReturnType<typeof updatedCard>
  | ReturnType<typeof deletedCard>
