import React, { FC, useState } from 'react';
import { Packs } from './Packs';
import { useDispatch } from 'react-redux';
import {
  addCardsPackTC,
  CardPacksType,
  deletedCardsPackTC,
  setErrorPacks,
  updatedCardsPackTC,
} from '../../bll/PacksReducer';
import { useTypedSelector } from '../../../../hooks/useTypedSelector';
import { PATH } from '../../../../main/ui/App/Routes';
import { Redirect } from 'react-router-dom';

export const PacksContainer: FC = () => {
  const dispatch = useDispatch();
  const { LOGIN } = PATH;

  const error = useTypedSelector<string>((state) => state.packs.error);
  const cardPacks = useTypedSelector<CardPacksType[]>((state) => state.packs.cardPacks);
  const loading = useTypedSelector<boolean>((state) => state.packs.loading);
  const cardPacksTotalCount = useTypedSelector<number>((state) => state.packs.cardPacksTotalCount);
  const userId = useTypedSelector<string>((state) => state.login.user._id);
  const [page, setPage] = useState<number>(1);
  const [pageCount, setPageCount] = useState<number>(4);

  if (userId === '') {
    return <Redirect to={LOGIN} />;
  }

  const deletedCardsPack = (cardsPackId: string) => {
    dispatch(deletedCardsPackTC(cardsPackId, "", cardPacksTotalCount, 1));
  };
  const updatedCardsPack = (changedPackName: string, cardsPackId: string) => {
    dispatch(updatedCardsPackTC(changedPackName, cardsPackId, '', cardPacksTotalCount, 1));
  };
  const addCardsPack = (name: string, privat: boolean) => {
    dispatch(addCardsPackTC(name, privat, '', cardPacksTotalCount, 1));
  };
  const closeMessage = (error: string) => {
    dispatch(setErrorPacks(error));
  };

  return <Packs
    userId={userId}
    deletedCardsPack={deletedCardsPack}
    updatedCardsPack={updatedCardsPack}
    addCardsPack={addCardsPack}
    cardPacks={cardPacks}
    loading={loading}
    closeMessage={closeMessage}
    error={error}
    cardPacksTotalCount={cardPacksTotalCount}
    page={page}
    setPage={setPage}
    pageCount={pageCount}
    setPageCount={setPageCount}
  />;
};
