import React, { FC } from 'react';

import s from './InfoMessage.module.scss';
import { Preloader } from '../Preloader';
import { ErrorMessage } from '../ErrorMessage';
import { ActionsType } from '../../../pages/cards/bll/CardsReducer';
import { useDispatch } from 'react-redux';

type PropsType = {
  error: string;
  loading: boolean;
  action: ActionsType;
};

export const InfoMessage: FC<PropsType> = ({ error, loading, action }) => {
  const dispatch = useDispatch();

  const closeMessage = () => {
    dispatch(action);
  };
  return (
    <div className={s.messageWrapper}>
      {loading && <Preloader text='Sending...' />}
      {!error && (
        <ErrorMessage clickHandler={closeMessage}>
          {error}
        </ErrorMessage>
      )}
    </div>
  );
};
