import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { useParams } from 'react-router-dom';
import { useTypedSelector } from '../../../../hooks/useTypedSelector';
import { CardsType } from '../../../cards/dal/CardsApi';
import { Button } from '../../../../common/ui/Button';
import { Preloader } from '../../../../common/ui/Preloader';
import {
  getCardsTC,
  setErrorCards,
  updateGradeCardTC,
} from '../../../cards/bll/CardsReducer';
import s from './TrainForm.module.scss';
import { ErrorMessage } from '../../../../common/ui/ErrorMessage';


interface ParamType {
  id: string
}

const grades = [' ', '', ' ', '', ''];

const getCard = (cards: CardsType[]) => {
  const sum = cards.reduce((acc, card) =>
    acc + (6 - card.grade) * (6 - card.grade), 0);
  const rand = Math.random() * sum;
  const res = cards.reduce((acc: { sum: number, id: number }, card, i) => {
      const newSum = acc.sum + (6 - card.grade) * (6 - card.grade);
      return { sum: newSum, id: newSum < rand ? i : acc.id };
    }
    , { sum: 0, id: -1 });
  console.log('test: ', sum, rand, res);
  return cards[res.id + 1];
};

export const TrainForm = () => {

  const [isChecked, setIsChecked] = useState<boolean>(false);
  const [first, setFirst] = useState<boolean>(true);
  const [btn, setBtn] = useState<number>(-1);
  // const [first, setFirst] = useState<boolean>(0);
  const { cards } = useTypedSelector((state) => state.cards);
  const error = useTypedSelector<string>((state) => state.cards.error);

  const [card, setCard] = useState<CardsType>({
    _id: 'fake',
    cardsPack_id: '',
    user_id: '',

    answer: 'answer fake',
    question: 'question fake',
    grade: 0,
    shots: 0,

    type: '',
    rating: 0,
    __v: 0,
    created: '',
    updated: '',
  });

  const loading = useTypedSelector<boolean>((state) => state.cards.loading);

  const { cardsPack_id } = useParams<{ cardsPack_id: string }>();
  const dispatch: Function = useDispatch();

  useEffect(() => {
    console.log('LearnContainer useEffect');

    if (first) {
      dispatch(getCardsTC(cardsPack_id));
      setFirst(false);
    }

    console.log('cards', cards);
    if (cards.length > 0) setCard(getCard(cards));

    return () => {
      console.log('LearnContainer useEffect off');
    };
  }, [dispatch, cardsPack_id, cards, first]);


  const onN = (i: number) => {
    console.log(i);
    setBtn(i);
  };


  const onNext = () => {
    setIsChecked(false);

    if (cards.length > 0) {
      dispatch(updateGradeCardTC(card.cardsPack_id, card._id, btn + 1));
      setCard(getCard(cards));
      setBtn(-1);
    } else {

    }
  };
  const closeMessageHandler = () => {
    dispatch(setErrorCards(''));
  };

  return (
    <div>
      <div className={s.form}>
        {loading && <Preloader text='Sending...' />}
        {error && (
          <ErrorMessage clickHandler={closeMessageHandler}>
            {error}
          </ErrorMessage>
        )}
      </div>

      <div className={s.trainBlock}>
        <div>{card.question}</div>
        <div>
          <Button onClick={() => setIsChecked(true)}>check</Button>
        </div>

        {isChecked && (
          <>
            <div>{card.answer}</div>

            <div className={s.grade}>
              {grades.map((g, i) => (
                <Button className={btn === i ? s.active : ''} key={'grade-' + i}
                        onClick={() => {
                          onN(i);
                        }}>{g}</Button>
              ))}
            </div>

            <div><Button onClick={onNext}>next</Button></div>
          </>
        )}
      </div>
    </div>
  );
};
