import { ThunkType } from '../../../main/bll/store';
import { CardsPackAPIType, packsAPI } from '../dal/PacksApi';

export enum packsActionType {
  SET_LOADING = 'CARDS/PACKS/SET_LOADING',
  SET_ERROR_PACKS = 'CARDS/PACKS/SET_ERROR_PACKS',
  SET_PACKS = 'CARDS/PACKS/SET_PACKS',
  ADD_PACK = 'CARDS/PACKS/ADD_PACK',
  UPDATE_PACKS = 'CARDS/PACKS/UPDATE_PACKS',
  DELETE_PACK = 'CARDS/PACKS/DELETE_PACK',
}

const initialState: StateType = {
  cardPacks: [],
  loading: false,
  error: '',
  cardPacksTotalCount: 0,
  maxCardsCount: 0,
  minCardsCount: 0,
  page: 1,
  pageCount: 100,
};

export const packsReducer = (
  state = initialState,
  action: ActionsType,
): StateType => {
  switch (action.type) {
    case packsActionType.SET_LOADING:
      return { ...state, error: '', loading: action.payload.loading };
    case packsActionType.SET_ERROR_PACKS:
      return {
        ...state, loading: false,
        error: action.payload.error,
      };
    case packsActionType.SET_PACKS:
      return { ...state, ...action.payload };
    default:
      return state;
  }
};

/** Actions */
export const setLoading = (loading: boolean) => ({
  type: packsActionType.SET_LOADING,
  payload: {
    loading,
  },
} as const);
export const setCardsPacks = (payload: CardsPackAPIType) => ({ type: packsActionType.SET_PACKS, payload } as const);
export const addCardsPack = (newCardsPack: CardPacksType) => ({
  type: packsActionType.ADD_PACK,
  newCardsPack,
} as const);
export const setErrorPacks = (error: string) =>
  ({
    type: packsActionType.SET_ERROR_PACKS,
    payload: {
      error,
    },
  } as const);
export const updatedCardsPack = (updatedCardsPack: CardPacksType) => ({
  type: packsActionType.UPDATE_PACKS, updatedCardsPack} as const);
export const deletedCardsPack = (deletedCardsPack: CardPacksType) => ({
  type: packsActionType.DELETE_PACK, deletedCardsPack} as const);

/** Thunks */
export const getCardsPacksTC = (userId: string, pageCount: number, page: number): ThunkType<ActionsType> =>
  async (dispatch) => {
    dispatch(setLoading(true));
    try {
      // dispatch(setErrorPacks(''));
      const data = await packsAPI.getPacks(userId, pageCount, page);
      dispatch(setCardsPacks(data));
      dispatch(setLoading(false));
    } catch (e) {
      const error = e.response
        ? e.response.data.error
        : (e.message + ', more details in the console');
      dispatch(setErrorPacks(error));
    }
  };
export const addCardsPackTC = (name: string, privat: boolean, userId: string, pageCount: number, page: number): ThunkType<ActionsType> =>
  async (dispatch) => {
    try {
      dispatch(setLoading(true));
      let data = await packsAPI.addPack(name, privat);
      await dispatch(getCardsPacksTC(userId, pageCount, page));
      dispatch(setLoading(false));
    } catch (e) {
      const error = e.response
        ? e.response.data.error
        : (e.message + ', more details in the console');
      dispatch(setErrorPacks(error));
    }
  };
export const updatedCardsPackTC = (changedPackName: string, cardsPackId: string, userId: string, pageCount: number, page: number): ThunkType<ActionsType> =>
  async (dispatch) => {
    try {
      dispatch(setLoading(true));
      let data = await packsAPI.updatedPacks(changedPackName, cardsPackId);
      await dispatch(getCardsPacksTC(userId, pageCount, page));
      dispatch(setLoading(false));
    } catch (e) {
      const error = e.response
        ? e.response.data.error
        : (e.message + ', more details in the console');
      dispatch(setErrorPacks(error));
    }
  };
export const deletedCardsPackTC = (cardsPackId: string, userId: string, pageCount: number, page: number): ThunkType<ActionsType> =>
  async (dispatch) => {
    try {
      dispatch(setLoading(true));
      let data = await packsAPI.deletePack(cardsPackId);
      await dispatch(getCardsPacksTC(userId, pageCount, page));
      dispatch(setLoading(false));
    } catch (e) {
      const error = e.response
        ? e.response.data.error
        : (e.message + ', more details in the console');
      dispatch(setErrorPacks(error));
    }
  };

/** Types */
export type StateType = {
  cardPacks: CardPacksType[];
  cardPacksTotalCount: number //  
  maxCardsCount: number
  minCardsCount: number
  page: number //  
  pageCount: number //    
  loading: boolean;
  error: string;
};

type ActionsType = ReturnType<typeof setLoading>
  | ReturnType<typeof setCardsPacks>
  | ReturnType<typeof addCardsPack>
  | ReturnType<typeof setErrorPacks>
  | ReturnType<typeof updatedCardsPack>
  | ReturnType<typeof deletedCardsPack>

export type CardPacksType = {
  _id: string
  user_id: string
  name: string
  path: string // 
  cardsCount: number
  grade: number //   
  shots: number //  
  rating: number // 
  type: string //   "folder" ()
  created: string
  updated: string
  __v: number
  private?: boolean
}